.formfield {
    margin-bottom: 30px;

    input {
        font-family: $font;
        font-size: 16px;
        font-weight: 300;
        width: 100%;
        height: 32px;
        border: 0;
        background-color: transparent;
        border-bottom: .5px solid #707070;
        outline: none;

        &:focus {
            box-shadow: none;
            border-bottom-color: $theme;
        }

        &::placeholder {
            font-weight: 300;
            color: $black;
            opacity: 1;
        }
    }

    &--submit {
        margin-top: 70px;

        input[type="submit"],
        button[type="submit"] {
            font-family: $font;
            font-size: 21px;
            font-weight: 300;
            padding: 17.5px 38px;
            text-align: left;
            border-radius: 28px;
            font-style: italic;
            color: #fff;
            background-color: $theme;
            border: 0;
            min-width: 287px;
            outline: none;
        }
    }
}